//반응형
@mixin media($breakpoint, $breakpoints:$breakpointText){
    @if map-has-key($breakpoints, $breakpoint){
        $breakpoint:map-get($breakpoints, $breakpoint);
    }

    @media screen and (max-width: #{$breakpoint}) {
        @content;
    }
}

$breakpointText: (
    mobileDeep: 410px,
    mobile:767px,
    tablet:1023px,
    tablet02 : 1160px,
    tablet03 : 1350px,
    tablet04 : 1585px
);